$bg: rgb(16, 16, 16);
.banner{
  height: 600px;
  background-color: $bg;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 3rem;
  box-sizing: border-box;
  
    &::after{
      content: " ";
      width: 100%;
      height: 100%;
      position: absolute;
      background: $bg;
      opacity: 0.4;
      top: 0%;
      left: 0%;
      background: $bg;
      

    }
    h1,p{
      color: white;
      font-size: 4rem;
      width: 40%;
      z-index: 10;
      // background-color: none;

    }
    p{
      font-size: 1.2rem;
      letter-spacing: 2px;
      word-spacing: 3px;
      font-weight: 100;


    }
    >div{
      display: flex;
      
      button{
        padding: 1rem;
        width: 150px;
        outline: none;
        border: none;
        z-index: 10;
        border-radius: 5px;
        margin: 1rem;
        cursor: pointer;
        font-size: 1.2rem;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.5s;
        &:hover{
          opacity: 0.6;
        }
        &:nth-child(odd){
          background-color: white;
          color: black;


        }
        &:nth-child(even){
          background-color: rgb(57, 57, 57);
          color: rgb(255, 255, 255);
        }
        svg{
          margin: 0 10px;
          
          font-size: 1.7rem;
        }

      }
    }
}
.row{
  background-color: $bg;
  padding: 1rem;

   >h2{
    color: white;
    text-decoration: none;
    font-weight: 400;

   }
    >div{
        display: flex;
      padding: 1rem;

        overflow-x: auto;
    }
}
.card{
  width: 200px;  
  cursor: pointer;
  margin: 0.3rem;
  transition:transform 0.5s;
  &:hover{
    transform: translateY(-10px) scale(1.05);
  }
}
.genrebox{
    overflow-x: auto;
  display: flex;
  height: 100px;
  background-color: $bg;
  flex: 0%;
    >a{
        color: black;
        background-color: white;
        padding: 1rem;
        text-align: center;
        border-radius: 5px;
        transition: all 0.5s;
        min-width: 10rem;
        display: flex;
        align-items: center;
        justify-content: center;
        justify-content: center;
        margin: 1rem;
        
        &:hover{
          background-color: rbg(236,236,236);

        }
    }
}